.container__mobile-menu {
    position: relative;
}
.mobile-menu {
    position: fixed;
    top: 0;
    left: 0;

    padding: 60px 20px;
    display: flex;
    flex-direction: column;

    background: $mobile-menu-bgc;

    width: 100vw;
    max-width: 250px;
    height: 100vh;
    z-index: 9;

    // opacity: 0;
    // transform: translateX(100%);
    // transition: transform 250ms ease, opacity 250ms ease;

    @include mq(desktop) {
        display: none;
    }
}

.mobile-menu.is-open {
    opacity: 1;
    transform: translateX(0);
}

.mobile-menu__list {
    padding: 0;
    margin: 0;
}

.mobile-menu__item {
    padding: 11px 0;
    @include fonts(14px, 1.3);
    font-weight: 700;
    text-align: start;

    color: #FFFFFF;
    border-top: 1px solid #E18298;
    
    &:hover,
    &:focus {
        color: $accent-title-color;
    }
    &:last-child {
    border-bottom: 1px solid #E18298;
    }
}


.mobile__mini-btn {
    position: absolute;

    display: inline-flex;
    align-items: center;
    justify-content: center;

    border: none;
    background-color: transparent;

    top: 8px;
    right: 8px;

    width: 30px;
    height: 30px;
    padding: 0;
    margin: 0;

    fill: $accent-title-color;

    border-radius: 50%;

    z-index: 10;

    @include mq(desktop) {
        display: none;
    }

    &__icon {
        width: 18px;
        height: 18px;
    }

    &.is-active .icon-open {
        opacity: 0;
    }

    &.is-active .icon-close {
        opacity: 1;
    }
}

.icon-open {
    opacity: 1;
    transition: transform 250ms ease, opacity 250ms ease;
}

.icon-close {
    fill: $accent-mode-btn;
    opacity: 0;
    transition: transform 250ms ease, opacity 250ms ease;
}


.mobile-btn {
    color: $ligth-mode-btn;
    background-color: $ligth-mode-btn-bgc;
    border-radius: 22px;

    outline: transparent;
    border: 1px solid rgba(0, 0, 0, 0);
    transition: background-color 250ms var(--kubik-rubik);
    
    font-weight: 700;
    @include fonts(14px, 1.3);

    padding: 12px 48px 12px 60px;
    margin-bottom: 32px;
    position: relative;
    display: flex;

    &:hover,
    &:focus {
        background-color: $ligth-mode-btn-bgc-hover;
    }

    &__icon {
        margin-left: 20px;
    }
}

.icon-arrow{
    width: 24px;
    height: 24px;
    margin-left: 10px;

    fill: currentColor;
}

.mobile-menu__link {
    outline: transparent;
    color: currentColor;
}